app.controller('HourlyRatesController', ['$scope', '$modalInstance', '$q', 'configurationService', 'pivottableService',
  'loggedInUser', 'translations', '$timeout', 'currencySymbol', 'changed', 'config',
  function ($scope, $modalInstance, $q, configurationService, pivottableService,
    loggedInUser, translations, $timeout, currencySymbol, changed, config) {
    $scope.saveCurrencySymbol = function () {
      $scope.loading = true
      changed.changed = true
      if (!$scope.currencySymbol.val) {
        delete $scope.currencySymbol.val
      }
      configurationService.saveConfiguration($scope.currencySymbol).then(function (config) {
        delete $scope.loading
        $scope.currencySymbol = config
      })
    }

    var noUser = { accountId: '', groups: {} }
    var restrictedGroups = config.restrictedGroups.val && config.restrictedGroups.val.filter(group => TimesheetUtils.findByProperty('name', group, loggedInUser.groups.items))
    $scope.restrictedGroups = restrictedGroups && (restrictedGroups.join(',') || 'NONE')
    var isUserAllowed = user => !restrictedGroups ||
      !user.accountId ||
      user.accountId === loggedInUser.accountId ||
      restrictedGroups.filter(group => TimesheetUtils.findByProperty('name', group, user.groups.items)).length
    var sortHourlyRates = hourlyRates => Object.keys(hourlyRates)
      .sort((a, b) => PivotKey.StringComparator($scope.users[a].displayName, $scope.users[b].displayName))
      .reduce((acc, accountId) => {
        acc[accountId] = Object.keys(hourlyRates[accountId])
          .sort()
          .reduce((acc, startDate) => {
            acc[startDate] = hourlyRates[accountId][startDate]
            return acc
          }, {})
        return acc
      }, {})

    $scope.addHourlyRate = function (user, startDate, rate) {
      if (!user) user = noUser
      if (isUserAllowed(user)) {
        var date = (startDate && TimesheetUtils.formatDateForSearch(moment(startDate).toDate())) || ''
        $scope.loading = true
        changed.changed = true
        if (!user) user = noUser
        configurationService.saveHourlyRate(user.accountId, date, rate).then(() => {
          delete $scope.loading
          $scope.hourlyRates[user.accountId] = $scope.hourlyRates[user.accountId] || {}
          $scope.hourlyRates[user.accountId][date] = rate
          if (user.accountId && !$scope.users[user.accountId]) {
            ($scope.users[user.accountId] = $scope.paramUserInfo)
          }
          $scope.hourlyRates = sortHourlyRates($scope.hourlyRates)
        })
      } else {
        AP.flag.create({
          title: translations['Denied'],
          body: translations['You can not add rates for this user'],
          type: 'error',
          close: 'auto'
        })
      }
    }

    $scope.removeHourlyRate = function (accountId, startDate) {
      $scope.loading = true
      changed.changed = true
      configurationService.deleteHourlyRate(accountId, startDate).then(function () {
        delete $scope.loading
        delete $scope.hourlyRates[accountId][startDate]
        if (!Object.keys($scope.hourlyRates[accountId]).length) {
          delete $scope.hourlyRates[accountId]
        }
      })
    }

    $scope.userChange = function (accountId) {
      pivottableService.getUserInfo(accountId).then(user => { $scope.paramUserInfo = user })
    }

    var $hourlyRates = configurationService.getHourlyRates().then(function (hourlyRates) {
      if (!hourlyRates) return {}
      var promises = []
      Object.keys(hourlyRates).forEach(accountId => {
        promises.push(pivottableService.getUserInfo(accountId))
      })
      return $q.all(promises).then(function (users) {
        users.forEach(user => {
          if (isUserAllowed(user)) {
            $scope.users[user.accountId] = user
          } else {
            delete hourlyRates[user.accountId]
          }
        })
        return hourlyRates
      })
    })

    $hourlyRates.then(hourlyRates => {
      $scope.hourlyRates = sortHourlyRates(hourlyRates)
    })

    $scope.cancel = function () {
      $modalInstance.dismiss()
    }

    $scope.currencySymbol = currencySymbol
    $scope.users = {}
    $scope.startDateUsers = {}
    $scope.hourlyRatesUsers = {}
  }
])
